body{
    margin: 0;
    font-family: sans-serif;
}

.header{
    background-color: #3b476a;
    color: #fff;
    font-size: 2em;
    height: 50px;
    padding: 20px;
}

.todoform{
    margin: auto;
    margin-top: 20px;
    width: 100%;
}

.todoinput{
    border: none;
    border-bottom: 1px solid whitesmoke;
    font-size: 1.5em;
    text-align: center;
    height: 50px;
    width: 100%;
}

.todobtn{
    border: none;
    background-color: #3b476a;
    color: #fff;
    font-size: 1.2em;
    height: 50px;
    margin-top: 20px;
    width: 100%;
    cursor: pointer;
}

.todobtn:hover{
    background-color: #353d54;
}

.todoitem{
    margin: auto;
    border: 2px solid #f5f5f5;
    padding: 20px;
    padding-top: 20px;
}

.deletebtn{
    border: none;
    background-color: #3b476a;
    border-radius: 3px;
    color: whitesmoke;
    font-size: 1.2em;
    height: 50px;
    cursor: pointer;
    width: 100px;
}
